<template>
  <div class="row">
    <el-tabs v-model="activeName" class="full-width">
      <el-tab-pane
        v-for="(tab, index) of settingView.tabs"
        :label="tab.title"
        :name="tab.id"
      >
        <form-card :group="group" v-for="group of tab.groups" v-model="settingData" :value="settingData"></form-card>
      </el-tab-pane>
    </el-tabs>
    <div class="container-fluid">
      <div class="page-action">
        <el-button @click="cancel">Hy</el-button>
        <el-button type="primary" @click="save">Cp nht</el-button>
      </div>
    </div>

    <el-dialog title="Phc hi phin bn" :visible.sync="dialogFormRestoreVisible" width="30%" @keyup.enter.native="restore">
      <el-form :model="formRestore">
        <el-form-item label="Phin bn:">
          <my-select
            :attribute="versionSetting"
            placeholder="Chn phin bn"
            v-model="formRestore.version"
          ></my-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogFormRestoreVisible = false">ng</el-button>
        <el-button type="primary" @click="restore">Phc hi</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
  import {Select, Option, Button, Tabs, TabPane, Message, Dialog, Form, FormItem} from 'element-ui'
  import FormCard from 'src/components/UIComponents/FormCard.vue'
  import MySelect from 'src/components/UIComponents/Select'
  import { mapState } from 'vuex'
  let _setting_views = {}, _setting_data = {};

  export default{
    components: {
      ElSelect: Select,
      ElOption: Option,
      ElButton: Button,
      ElTabs: Tabs,
      ElTabPane: TabPane,
      ElFormItem: FormItem,
      ElDialog: Dialog,
      ElForm: Form,
      FormCard,
      Message,
      MySelect
    },
    beforeCreate() {
      if (this.$store.state.settingsView) {
        let settingView = this.$store.state.settingsView
        _setting_views = settingView;

        settingView.tabs.forEach(function (tab) {
          tab.groups.forEach(group => {
            group.attributes.forEach(attr => {
              if (attr.multiple) {
                _setting_data[attr.name] = [];
              } else {
                _setting_data[attr.name] = '';
              }
            })
          })
        })
      }
    },
    computed: {
      ...mapState(['settings', 'versionSetting']),
      activeName(){
        return this.settingView.tabs[0].id||'home';
      }
    },
    data () {
      return {
        dialogFormRestoreVisible: false,
        formRestore: {},
        listVersion: [],
        settingView: _setting_views,
        settingData: _setting_data
      }
    },
    mounted() {
      this.$store.dispatch('fetchSettingData');
      this.$store.dispatch('fetchVersionSettings');

      this.$store.dispatch('setPageTitle', 'Ty bin giao din');
      this.$store.dispatch('setCurrentActions', [{
        label: 'Phc hi phin bn',
        type: 'default',
        icon: '',
        callback: this.showRestore
      },{
        label: 'Cp nht',
        type: 'primary',
        icon: '',
        callback: this.save
      }])
    },
    methods: {
      cancel() {
        this.$router.push({name: 'dashboard'});
      },
      showRestore(){
        this.dialogFormRestoreVisible = true;
      },
      restore(){
        let self = this;
        this.$store.dispatch('loadSettingData', self.formRestore.version).then(result => {
          self.dialogFormRestoreVisible = false;
        }, error => {
          Message({
            message: 'C li xy ra',
            type: 'danger'
          });
        });
      },
      save() {
        let self = this;
        let data = this.settingData;
        this.$store.dispatch('storeSettings', data).then(res => {
          Message({
            message: 'Cp nht thnh cng',
            type: 'success'
          });
        }, res => {
          Message({
            message: res.message,
            type: 'danger'
          });
        });
      }
    },
    watch: {
      settings(newValue, oldValue) {
        this.settingData = newValue;
      }
    },
    destroyed(){
      this.$store.dispatch('setCurrentActions', []);
      this.$store.state.settings = [];
    }
  }
</script>
<style lang="scss">
  .el-table .td-actions{
    button.btn {
      margin-right: 5px;
    }
  }
  .full-width{
    width: 100% !important;
  }
</style>
